import {
  Box,
  Card,
  CardHeader,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
  Checkbox
} from '@mui/material'
import { FC } from 'react'
import { Conference } from 'src/models/Conference'

interface AttendanceTableProps {
  className?: string
  conference: Conference
  handleAttendanceCheck(conferenceId: number, userId: number, isAttendance: boolean): Promise<void>
}

const AttendanceTable: FC<AttendanceTableProps> = ({ conference, handleAttendanceCheck }) => {
  return (
    <Card>
      <CardHeader title={conference.title} />
      <Divider />
      <TableContainer sx={{ height: '50vh' }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>이름</TableCell>
              <TableCell>학번</TableCell>
              <TableCell>학과</TableCell>
              <TableCell>출석 여부</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {conference.member.map(member => {
              return (
                <TableRow hover key={member.id}>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      <Box style={{ display: 'flex', alignItems: 'baseline' }}>{member.name}</Box>
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {member.studentId}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Typography variant="body1" fontWeight="bold" color="text.primary" gutterBottom noWrap>
                      {member.department}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Checkbox
                      defaultChecked={member.attendanceType}
                      onChange={e =>
                        handleAttendanceCheck(conference.id, member.id, (e.target as HTMLInputElement).checked)
                      }
                    />
                  </TableCell>
                </TableRow>
              )
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </Card>
  )
}

export default AttendanceTable
